	import {
		hostUrl
	} from '@/http/request.js';
import {mapState} from 'vuex';
	export default {
		data() {
			return {
				hostUrl: hostUrl
				
			}
		},
		mounted() {
		},
		computed:{
			...mapState({
				indexType:state => state.home.indexType
			})
		},
		methods:{
			goGoodList(id){
				uni.navigateTo({
					url:`/pages/goodList/good-list?typeId=${id}&type=0`
				})
			},
			goDetail(id){
				uni.navigateTo({
					url:`/pages/details/details?id=${id}`
				})
			}
		}
	}
